import React, {Component} from 'react';
import {Grid, Cell, List, ListItem, ListItemContent} from 'react-mdl';

class Contact extends Component{
  render(){
    return(
      <div className = "contact-body">
      <Grid className="contact-grid">
      <Cell col={6}>
      <h2>Nicholas Montgomery</h2>
      <img
      src="https://cdn2.iconfinder.com/data/icons/avatar-2/512/Fred_man-512.png"
      alt="avatar"
      style={{height:"250px"}}
      />
      <p style ={{width:"75%", margin:"auto", paddingTop: "1 em"}}>Contact me anytime! The best and fastest way to reach me is over the phone, but email is always acceptable as well!</p>
      </Cell>
      <Cell col={6}>
      <h2>Contact Me!</h2>
      <hr />
<div className="contact-list"><List>
<ListItem>
<ListItemContent style={{fontsize:"30px", fontFamily:"Anton"}}>
<i className= "fa fa-phone-square" aria-hidden ="true" />
949-619-9155
</ListItemContent>

</ListItem>
<ListItem>
<ListItemContent style={{fontsize:"30px", fontFamily:"Anton"}}>
<i className= "fa fa-envelope" aria-hidden ="true" />
montgomerynicholas30@gmail.com
</ListItemContent>

</ListItem>
</List></div>

      </Cell>
      </Grid>
      </div>

    )
  }
}
export default Contact;
